input {
    padding-top: 15px;
    padding-bottom: 15px;
    padding-left: 60px;
    padding-right: 15px;;
    border: 1px solid #000000;
    outline: none;
}
input[type='email'],
input[type='text'],
input[type='password'] {
    width: 100%;
}
input[type=checkbox] {
    transform: scale(1.5);
}
input::focus {
    outline: none;;
}
.input-group {
    position: relative;
    margin-bottom: 15px;
}
.input-icon {
    width: 25px;
    position: absolute;
    top: 50%;
    font-size: 20px;
    left: 20px;
    transform: translateY(-50%);
    z-index: 1;
}
.show-pass-icon {
    width: 25px;
    position: absolute;
    top: 50%;
    right: 20px;
    transform: translateY(-50%);
    cursor: pointer;
}